/*
 * 2.2 Header
 *****************************************************************************************************************************/
.header {
  height: 100rem;
  height: 100vh;
  background: #fff url("assets/img/desk.jpg") no-repeat;
  background-size: cover;
  text-align: center; }

.header-overlay {
  height: 100%;
  background-color: rgba(35, 45, 56, 0.95);
  animation: bg-color-change  15s linear infinite; }

.header-content {
  position: relative;
  top: 50%;
  transform: translateY(-50%); }
  .header-content * {
    color: #fff; }
  .header-content > h1 {
    font-family: "Oswald", serif;
    font-size: 7.5rem; }
  .header-content > a {
    animation: move-down 2.5s ease-in-out infinite; }

/*
 * 2.3 Footer
 *****************************************************************************************************************************/
.footer {
  margin-top: 6rem;
  border-top: 1px solid #ddd;
  padding-top: 6rem;
  padding-bottom: 6rem;
  text-align: center; }

.footer .container, .footer .header-content, .footer .section {
  width: 30rem; }

/*
 * 2.4 Forms
 *****************************************************************************************************************************/
/* Form focus effects inspired by
http://tympanus.net/Development/TextInputEffects/ */
form {
  margin-top: -3rem; }

input,
textarea,
.form-group {
  display: block;
  width: 100%; }

input,
textarea {
  border-width: 0 0 .1rem 0;
  border-style: solid;
  border-color: #ddd;
  color: #353535;
  background-color: #fff;
  font-family: "Raleway", sans-serif;
  font-size: 1.4rem; }

input {
  height: 4rem; }

textarea {
  height: 12rem;
  resize: none; }

.form-group {
  position: relative;
  padding-top: 3rem; }

.form-group:not(:last-child) {
  margin-bottom: 3rem; }

label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden; }

label > span,
label::after {
  transition: all ease-in-out 250ms; }

label > span {
  display: inline-block;
  transform: translateY(3rem); }

label::after {
  content: ' ';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: .4rem;
  background-color: #546c86;
  transform: translateX(-100%); }

.form-group:nth-child(2n) label::after {
  background-color: #6c71a2; }

.form-group:nth-child(3n) label::after {
  background-color: #619898; }

input.filled + label > span,
textarea.filled + label > span {
  transform: translateY(0); }

input.filled + label::after,
textarea.filled + label::after,
input.error + label::after {
  transform: translateX(0); }

input.error + label::after {
  background-color: crimson; }
  .form-group:nth-child(2n) input.error + label::after, .form-group:nth-child(3n) input.error + label::after {
    background-color: crimson; }

/*
 * 2.5 Buttons
 *****************************************************************************************************************************/
.button {
  display: inline-block;
  border: 0;
  padding: 1.5rem 3rem;
  color: #fff;
  background-color: #546c86;
  text-transform: lowercase; }

/*
 * 4.0 Animations
 *****************************************************************************************************************************/
@keyframes bg-color-change {
  0%, 100% {
    background-color: rgba(35, 45, 56, 0.95); }
  25% {
    background-color: rgba(55, 58, 87, 0.95); }
  75% {
    background-color: rgba(47, 74, 74, 0.95); } }
@keyframes move-down {
  0%, 100% {
    transform: translateY(0); }
  50% {
    transform: translateY(0.5rem); } }
